import React, { Component } from 'react';
import Aux from 'react-aux';
import { TabContent, TabPane, Nav, NavItem, NavLink, Card, CardTitle, CardText, Row, Col } from 'reactstrap';
import classnames from 'classnames';
import Toolbar from '../../components/Navigation/Toolbar/Toolbar';
import Footer from '../../components/Navigation/Footer/Footer';
import './Faq.css';

class Faq extends Component {
    constructor(props) {
        super(props);

        this.toggle = this.toggle.bind(this);
        this.state = {
            activeTab: '1'
        };
    }

    componentWillMount() {
        document.title = 'Chatshier ';
    }

    toggle(tab) {
        if (this.state.activeTab !== tab) {
            this.setState({
                activeTab: tab
            });
        }
    }

    render() {
        return (
            <Aux>
                <Toolbar/>
                <div className="Faq">
                    <div><h1>Help Center</h1></div>
                    <Nav tabs>
                        <NavItem>
                            <NavLink
                                className={classnames({ active: this.state.activeTab === '1' })}
                                onClick={() => { this.toggle('1'); }}
                            >
                            Chatshier
                            </NavLink>
                        </NavItem>
                        <NavItem>
                            <NavLink
                                className={classnames({ active: this.state.activeTab === '2' })}
                                onClick={() => { this.toggle('2'); }}
                            >
                            
                            </NavLink>
                        </NavItem>
                        <NavItem>
                            <NavLink
                                className={classnames({ active: this.state.activeTab === '3' })}
                                onClick={() => { this.toggle('3'); }}
                            >
                            
                            </NavLink>
                        </NavItem>
                    </Nav>
                    <TabContent activeTab={this.state.activeTab}>
                        <TabPane tabId="1">
                            <Row>
                                <Col sm="6" className="col_margin">
                                    <Card body>
                                        <CardTitle></CardTitle>
                                        <CardText>
                                        
                                         </CardText>
                                    </Card>
                                </Col>
                                <Col sm="6" className="col_margin">
                                    <Card body>
                                        <CardTitle>Chatshier </CardTitle>
                                        <CardText> Chatshier  <a href="https://medium.com/@chatshier/%E8%A8%8A%E6%81%AF%E4%B8%B2%E6%8E%A5%E8%A8%AD%E5%AE%9A-e112254e25f5" target="_blank">LineFacebook</a></CardText>
                                    </Card>
                                </Col>
                            </Row>
                        </TabPane>
                        <TabPane tabId="2">
                            <Row>
                                <Col sm="6" className="col_margin">
                                    <Card body>
                                        <CardTitle></CardTitle>
                                        <CardText>

                                        20
                                        VIP......
                                        
                                        
                                        </CardText>
                                    </Card>
                                </Col>
                                <Col sm="6" className="col_margin">
                                    <Card body>
                                        <CardTitle></CardTitle>
                                        <CardText>
                                            <br/>
                                            <p>: </p>
                                            <ul>
                                                <li>  </li>
                                                <li>  </li>
                                                <li>  </li>
                                            </ul>
                                        </CardText>
                                    </Card>
                                </Col>
                            </Row>
                            <Row>
                                <Col sm="6" className="col_margin">
                                    <Card body>
                                        <CardTitle></CardTitle>
                                        <CardText>
                                            <br/>
                                            <p>: </p>
                                            <ul>
                                                <li>   : 24:00 ~ 08:00</li>
                                                <li>  </li>
                                            </ul>
                                        </CardText>
                                    </Card>
                                </Col>
                                <Col sm="6" className="col_margin">
                                    <Card body>
                                        <CardTitle></CardTitle>
                                        <CardText>

                                        
                                           :  
                                          </CardText>
                                    </Card>
                                </Col>
                            </Row>
                            <Row>
                                <Col sm="6" className="col_margin">
                                    <Card body>
                                        <CardTitle></CardTitle>
                                        <CardText>

                                        
                                          </CardText>
                                    </Card>
                                </Col>
                            </Row>
                        </TabPane>
                        <TabPane tabId="3">
                            <Row>
                                <Col sm="6" className="col_margin">
                                    <Card body>
                                        <CardTitle>Chatshier</CardTitle>
                                        <CardText>Chatshier   Facebook  LINE 
                                        </CardText>
                                    </Card>
                                </Col>
                                <Col sm="6" className="col_margin">
                                    <Card body>
                                        <CardTitle>Chatshier </CardTitle>
                                        <CardText> Chatshier 
                                        Facebook
                                        LINE 
                                        
                                        WeChat</CardText>
                                    </Card>
                                </Col>
                            </Row>
                            <Row>
                                <Col sm="6" className="col_margin">
                                    <Card body>
                                        <CardTitle></CardTitle>
                                        <CardText></CardText>
                                    </Card>
                                </Col>
                                <Col sm="6" className="col_margin">
                                    <Card body>
                                        <CardTitle></CardTitle>
                                        <CardText> 

                                         : 
                                         : ? 
                                         :  
                                        
                                         
                                        
                                         : 
                                         : ? 
                                         :  :  9:00~21:00 9:00~18:00</CardText>
                                    </Card>
                                </Col>
                            </Row>
                        </TabPane>
                    </TabContent>
                </div>
                <Footer />
            </Aux>
        );
    }
}

export default Faq;



